.App-header{
    height: 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background-color: hsl(220, 15%, 18%);
    color: hsl(220, 0%, 98%);
}
.App-logo{
    height: 60px;
    padding-right: 0.5rem;
}
.nav-item{
    color: hsl(220, 0%, 98%);
    text-decoration: none;
}

.empty-card {
    padding: 1rem;
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    background: #8c939a;
    color: #2d2c2c;
}